.Persik {
  display: block;
  width: 100%;
  max-width: 240px;
  margin: 20px auto;
}


@font-face {
  font-family: 'San Francisco Pro Display Thin';
  src: url('SF-Pro.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
  }


.box-name {font-family: 'San Francisco Pro Display Thin';}

.parent {
  position: relative;
}

.svg{
  position: absolute;
  left: 10%;
  top: 60%;
  -webkit-transform: translate(-10%, -60%);
  -moz-transform: translate(-10%, -60%);
  -ms-transform: translate(-10%, -60%);
  -o-transform: translate(-10%, -60%);
  transform: translate(-10%, -60%);
}

.title{

  position: absolute;
  left: 10%;
  top: 30%;
  -webkit-transform: translate(-10%, -30%);
  -moz-transform: translate(-10%, -30%);
  -ms-transform: translate(-10%, -30%);
  -o-transform: translate(-10%, -30%);
  transform: translate(-10%, -30%);

  size: 16;
}

.discription{
  position: absolute;
  left: 10%;
  top: 40%;
  -webkit-transform: translate(-10%, -40%);
  -moz-transform: translate(-10%, -40%);
  -ms-transform: translate(-10%, -40%);
  -o-transform: translate(-10%, -40%);
  transform: translate(-10%, -40%);
}
